import React, { createContext, useReducer } from 'react';
import { reducer } from './../reducer/reducer';
import { initialState } from './../reducer/initState';
import { SET_GOODS, CLOSE_ALERT, REMOVE_FROM_BASKET, ADD_TO_BASKET, INCREMENT_QUANTITY, DECREMENT_QUANTITY, TOGGLE_BASKET } from '../reducer/types';


export const ShopContext = createContext();

export const ContextProvider = ({ children }) => {
  // value -объект: ключи, которые ожидаем, goods, loading, order и т.д. и dispatch - функция обновления
  const [ value, dispatch ] = useReducer(reducer, initialState); 

  //переписываем функции из Shop. Если функция принимает арг, то записываем с payload
  value.closeAlert = () => {
    dispatch({ type: CLOSE_ALERT })
  };

  value.removeFromBasket = (itemId) => {
    dispatch({ type: REMOVE_FROM_BASKET, payload: {id: itemId} })
  };

  value.addToBasket = (item) => {
    dispatch({ type: ADD_TO_BASKET, payload: item })
  };

  value.incQuantity = (itemId) => {
    dispatch({ type:  INCREMENT_QUANTITY, payload: { id: itemId }})
  };

  value.decQuantity = (itemId) => {
    dispatch({ type:  DECREMENT_QUANTITY, payload: { id: itemId }})
  };

  value.handleBasketShow = () => {
    dispatch({ type: TOGGLE_BASKET })
  };

  value.setGoods = (data) => {
    dispatch({ type: SET_GOODS, payload: data })
  }; 

  return (
    <ShopContext.Provider value={value}>
      {children}
    </ShopContext.Provider>
  )
};